import React, { Component } from 'react';

const Projectcard= props =>(
    <div className="card-container padding-5">
        <img className="project-image" src={'../../public/img/'+props.photo} alt={props.title}/>
      <div className="project-info">
          
            <h2 className="text-center header">{props.title}</h2>
      
          <h3 className="text-center project-description"> {props.description}</h3>
          <h4> <i> {props.skills}</i></h4>

         <a className="project-link" href={props.website}> <button>Check it out </button></a> 
      </div>
    </div>
  )

export default Projectcard;
